.speech_bubble {
    // Speech bubble border parameters
    $border-width: 0.15rem;
    $triangle-height: 2rem;
    $triangle-width: 3rem;
    $triangle-offset: 2.8rem;

    // You'll have to work `alpha` and `sin(alpha)` out, sorry.

    // alpha = atan($triangle-height / $traingle-width);
    // => 0.5880026
    // triangle-oddwidth: $border-width / sin(alpha);
    $triangle-oddwidth: $border-width / 0.55470019;
    $triangle-fullwidth: $triangle-oddwidth + $border-width + $triangle-width;
    $triangle-oddheight: (($triangle-fullwidth * $triangle-height) / $triangle-width) - $triangle-height;

    border: $border-width solid;
    border-radius: 3rem;

    padding: 0.5rem 1.5rem;
    position: relative;


    margin-bottom: $triangle-height;

    max-width: 25rem;

    @at-root blockquote#{&} {
        background: inherit;
    }
    
    &:after,
    &:before {  
        content: '';
        position: absolute;

        display: block;
        bottom: 0 - $triangle-height;
        left: $triangle-offset;

        border-top: $triangle-height solid $main-background;
        border-left: $triangle-width solid transparent;
    }

        &:before {
            border-top-width: $triangle-height + $triangle-oddheight;
            border-left-width: $triangle-width + $border-width + $triangle-oddwidth;
            border-top-color: inherit;
            margin-bottom: - $triangle-oddheight;
            left: $triangle-offset - $triangle-oddwidth;
        }
}

.speech_figure {
    margin: $line-rem auto;
}