.citeblock {
    position: relative;
    cursor: pointer;

    &:before {
        content: '';

        color: $link-color;

        display: inline-block;
        font-size: 0.6rem;
        padding: 0.1em;

        border: 0.1em solid;
        border-radius: 0.2em;
        vertical-align: super;

        line-height: 1;
    }
    
    .citation {
        display: none;
        position: absolute;
        background: $main-background;
        padding: 0.5rem;
        border: 1px solid;
        border-radius: 0.2rem;
        width: max-content;
        max-width: 20rem;

        &:hover, &:focus {
            display: initial;
        }
    }

    &:hover, &:focus {
        &:before {
            background: $link-hover-color;
            color: $main-background;
            border-color: $link-hover-color;
        }

        .citation {
            display: initial;
            z-index: 1000;
        }
    }
}