body.home {
	@include global_grid;

	header {
		grid-column: content;
	}

	section {
		grid-column: article;
	}

	.hero {
		grid-column: full;

		@include global_grid;
	}

		.hero__question {
			grid-column: content;

			@include breakpoint($begin-columns) {
				margin-top: 2vh;
			}

			h1 {
				display: inline;
				margin: 0;
			}
		}

		.hero__maybe {
			color: $deemphasized-text;
		}

		.hero__image {
			grid-column: content;
			margin: 0 auto;
			margin-bottom: -7px;

			@include breakpoint($begin-columns) {
				margin-left: 0;
			}
		}

		.hero__answer {
			grid-column: full;

			@include global_grid;
			@include dark;

			padding: 2rem 0;

			& > * {
				grid-column: content;
			}
		}

			.hero__answer__super {
				font-size: 4rem;
				line-height: (3 * $line-height) / (4 * $base-font-size);

				@include breakpoint($begin-columns) {
					font-size: 7rem;
					line-height: (6 * $line-height) / (10 * $base-font-size);

					grid-row: 1;
				}

				padding: 0;
				margin: 0 0 $line-height 0;
			}

			.hero__answer__tagline {
				grid-column: article;

				font-size: 1.3rem;
			}

	footer {
		grid-column: full;
		grid-row: 5;
	}
}